@lost gutter 60px {}

body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: aller, sans-serif;
  background-color: $bgc;
  text-rendering: optimizeLegibility;
 }  // lost-utility: edit

.global-content {
  lost-center: 100%;
  position: relative;
  overflow: hidden;
  height: 100%; }

#skew-bg {
  z-index: -100;
  position: absolute;
  bottom: 0;
  left: -500px;
  right: 0;
  width: 100%;
  height: 100%; }

.container {
  lost-center: 1440px;
  padding: 5rem 3rem; }

ul,ol {
  list-style: none; }

strong {
  font-weight: 700; }


.global-footer {
  lost-center: 100%;
  margin-top: 15rem;
  padding: 2rem 0;
  color: $bgc;
  background: $darkblue;
  a {
    color: $bgc;
    border-color: $bgc; }
  .footer {
    lost-center: 960px;
    .contact {
      lost-column: 6/12 0 0; }
    .newsletter {
      lost-column: 6/12 0 0; }
    .copy {
      display: block;
      clear: both; } } }
